@font-face {
    font-family: 'NotoSansCJKkr-Medium';
	src : url(../fonts/NotoSansCJKkr-Medium.otf) format('truetype');
}

html, body {
    height: 100%;
    font-family : NotoSansCJKkr-Medium;
    background-color : #EFEEEE;
}

#header{
    height: 70px;
    position: fixed;
    top :0;
    width : 100%;
    z-index : 10;
}


#blogTitle{
    position: absolute;
    top:1.5%;
    left:5%;
}

#blogTitle a{
    color: black; 
    text-decoration: none;
}

#navMenu{
    position: absolute;
    top:1.5%;
    right:5%;
    list-style-type: none;
}

#navMenu li{
    display: inline-block;
    padding:0px 30px;
}

#navMenu li a{
    padding: 8px 16px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    cursor: pointer;
    border-radius: 50px;
    background: #EFEEEE;
    box-shadow:  5px 5px 10px #cbcaca, 
             -5px -5px 10px #ffffff;
    color: black;
}

#navMenu li a:hover{
    border-radius: 50px;
    background: linear-gradient(145deg, #d7d6d6, #ffffff);
    box-shadow:  20px 20px 60px #cbcaca, 
                 -20px -20px 60px #ffffff;
}

.clicked{
    color : gray !important;
    border-radius: 50px !important;
    background: #EFEEEE !important;
    box-shadow: inset 7px 7px 14px #cbcaca, 
                inset -7px -7px 14px #ffffff !important;
}

.content {
    color : gray;
}

#mainSection{
    display: flex;
    height: 85%;
    background-color: #ffffff;
}

#joelPicture{
    width:400px;
    height:400px;
    margin:auto;
}

#sideMenuBar {
    position : fixed;
    top: 13%;
    left: 3%;
    height : 50%;
    width : 260px;
    border-radius: 14px;
    background: #EFEEEE;
    box-shadow:  5px 5px 10px #acabab, 
             -5px -5px 10px #ffffff;
}

#sideMenuBar ul {
    padding : 5px;
}

.parentMenu{
    max-width:100%;
    list-style-type: none;
    color : black;
    padding : 10px;
    margin : 20px;
    font-size: 20px;
    font-weight: bold;
    
}

.childMenu{
    padding-left : 100px !important;
}

.childMenu li{
    font-size : 18px;
    list-style-type: none;
    color : black;
}

#postList{
    position : absolute;
    top: 100px;
    left: 400px;
    
}

#postStyle{
    position: relative;
    padding : 5% 8% 10% 8%;
    margin: 0% 15% 10% 15%;
    border-radius: 14px;
background: #EFEEEE;
box-shadow:  5px 5px 10px #acabab, 
             -5px -5px 10px #ffffff;
}

.postContent {
    list-style-type: none;
    padding : 30px;
    margin : 50px;
    cursor: pointer;
    border-radius: 14px;
    background: #EFEEEE;
    box-shadow:  5px 5px 10px #acabab, 
             -5px -5px 10px #ffffff;
}

.postContent:hover {
    border-radius: 14px;
    background: linear-gradient(145deg, #d7d6d6, #ffffff);
    box-shadow:  5px 5px 10px #acabab, 
                 -5px -5px 10px #ffffff;
}

.postContent a{
    text-decoration: none;
    color : black;
}

blockquote {
    display:block;
    background: #fff;
    margin: 0px 50px 0px 50px;
    padding: 30px 30px 30px 50px;
    position: relative;
    font-family: Georgia, serif;
    font-size: 18px;
    line-height: 1.2;
    color: #666;
    text-align: justify;
    border-left: 15px solid #c76c0c;
    border-right: 5px solid #c76c0c;
    -moz-box-shadow: 2px 2px 15px #ccc;
    -webkit-box-shadow: 2px 2px 15px #ccc;
    box-shadow: 2px 2px 15px #ccc;
}

.highlight { 
    background : black; 
    color : white; 
    padding: 10px; 
}